import React, { Component } from 'react';
import './App.css';
import request from 'superagent'
import Notifications, { notify } from 'react-notify-toast';

class App extends Component {
  state = {
    value: '',
    city: []
  };

  componentDidMount() {
    console.log(navigator.geolocation)
    request
      .get('https://api.weatherbit.io/v2.0/current')
      .query({ 'city': 'Belgrade' })
      .query({ 'key': 'd5c3391c63af47658c84946c5fe654e5' })
      .then(res => {
        console.log(res.body);
      });
  }

  handleChange = (event) => {
    this.setState({ value: event.target.value });
  }

  handleSubmit = (event) => {
    request
      .get('https://api.weatherbit.io/v2.0/current')
      .query({ 'city': this.state.value })
      .query({ 'key': 'd5c3391c63af47658c84946c5fe654e5' })
      .then(res => {
        if (res.body) {
          this.setState(prevState => ({
            city: [...prevState.city, res.body.data[0]]
          }));
        } else {
          notify.show('No results for choosen city');
        }
      })
      .catch(err => {
        notify.show(err.message);
      });
    event.preventDefault();
  }

  render() {

    const weather = this.state.city.map((city, index) => {
      return (
        <div key={index}>
          <h5>{city.city_name}</h5>
          <h6>{city.weather.description}</h6>
        </div>
      )
    })

    return (
      <div className="App">
        <Notifications />
        <header className="App-header">
          <form onSubmit={this.handleSubmit}>
            <input type="text" value={this.state.value} onChange={this.handleChange} />
            <input type="submit" value="Submit" className="btn btn-gray" />
          </form>
          {
            this.state.city
              ?
              weather
              :
              <h5>Populate me</h5>
          }
        </header>
      </div>
    );
  }
}

export default App;
